<template>
    <div class="setComponent">
        <div class="form-item">
            <label></label>
            <el-button size="small" type="primary" @click="uploadClick"></el-button>
        </div>
        <div class="form-item">
            <label>/px</label>
            <input type="text" v-model="obj.height">  
        </div>
        <div class="form-item">
            <label></label>
            <el-switch
                v-model="obj.ifThumbs"
            >
            </el-switch>   
        </div>
        <div class="form-item">
            <label></label>
            <el-switch
                v-model="obj.swiperOptionTop.loop"
            >
            </el-switch>   
        </div>
        <div class="form-item">
            <label></label>
            <el-switch
                v-model="obj.ifArrow"
            >
            </el-switch>   
            
        </div>
        <div class="form-item">
            <label></label>
            <el-switch
                v-model="obj.swiperOptionTop.autoplay"
            >
            </el-switch>         
        </div>
        <div class="form-item">
            <label></label>
            <el-select v-model="obj.swiperOptionTop.effect" placeholder="">
                <el-option
                    v-for="(item,index) in effectOptions"
                    :key="index"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>        
        </div>
        <div class="form-item">
            <label></label>
            <input type="text" v-model="obj.swiperOptionTop.slidesPerView">        
        </div>
        
    </div>
</template>
<script>
export default {
    name: 'Swiper',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            dialogVisible: false,
            effectOptions: [
                {
                    label: '',
                    value: 'slide'
                },
                {
                    label: '',
                    value: 'fade'
                },
                {
                    label: '',
                    value: 'cube'
                },
                {
                    label: '3d',
                    value: 'coverflow'
                },
                {
                    label: '3d',
                    value: 'flip'
                }
            ]
        }
    },
    methods: {
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        }
    }
}
</script>
<style lang="less" scoped>

</style>

